import React from 'react';
import { Component } from 'react';
import './Enquiry.css';
import firebasedb from './firebase';

class Enquiry extends Component {
    constructor(){
        super();
        this.state = {
            name:"",
            email:"",
            phoneno: "",
            query:""
        }
    this.doneSubmit = this.doneSubmit.bind(this);
    }
    doneSubmit(e){
        e.preventDefault();
        firebasedb.database().ref('UserDetails').orderByKey().limitToLast(100);
        firebasedb.database().ref('UserDetails').push(this.state);
    }
    render(){
        return (
            <div id="body">
                <form onSubmit={this.doneSubmit}>
                    <label>Name</label>
                    <input type="text" className="form-control col-sm-6 offset-sm-3" value={this.state.name} onChange = {(e)=>{
                        this.setState({
                            name:e.target.value
                        })
                    }} required/>
                    <label>E-mail</label>
                    <input type="mail" className="form-control col-sm-6 offset-sm-3" value={this.state.email} onChange = {(e)=>{
                        this.setState({
                            email:e.target.value
                        })
                    }}required/>
                    <label>Phone Number</label>
                    <input type="num" className="form-control col-sm-6 offset-sm-3" value={this.state.phoneno} onChange = {(e)=>{
                        this.setState({
                            phoneno:e.target.value
                        })
                    }}required/> <br />
                    <label>Query</label>
                    <input type="text" className="form-control col-sm-6 offset-sm-3" value={this.state.quefy} onChange = {(e)=>{
                        this.setState({
                            query:e.target.value
                        })
                    }}required/> <br />
                    <button id="submit" type="submit" className="btn btn-success col-sm-3"> Book Appointment </button>
                </form>
            </div>
        )
    }
}
export default Enquiry;