import React from 'react';
import { Component } from 'react';
import firebasedb from './firebase';

export default class Admin extends Component{
    constructor(){
        super();
        this.state = {
            Correctmail: false,
            uname:'',
            password:'',
            users:[]
        }
        this.checkemail = this.checkemail.bind(this);
    }
    componentDidMount() {

        firebasedb.database().ref("UserDetails").on("value", snapshot => {
          let Users = [];
          snapshot.forEach(snap => {
              Users.push(snap.val());
          });
          this.setState({ users: Users });
        })}
    checkemail(e){
        e.preventDefault();
        if ((this.state.uname === "vijaiastro5") && (this.state.password === "9842056022")){
            console.log("Admin Login Approved");
            this.setState({uname:'',password:'',Correctmail:true})
        }
        else{
            this.setState({
                Correctmail:false
            })
            document.getElementById('Error_Message').innerHTML = "Invalid Username or password!";
            document.getElementById('Error_Message1').innerHTML = "Try Again";
        }

    }
    render(){
        return(
            <div id="Adminlogin">
                {this.state.Correctmail === false
                ?
                <div style={{marginTop:"5%"}}>
                    <h2>Enter the Admin Login Details</h2>
                    <form onSubmit={this.checkemail}>
                        <label>UserName</label>
                        <input className="form-control col-sm-6 offset-sm-3" value={this.state.uname} onChange={(e) => {
                            this.setState({uname: e.target.value})
                        }} />
                        <label>Password</label>
                        <input type="password" className="form-control col-sm-6 offset-sm-3" value={this.state.password} onChange={(e) => {
                            this.setState({password:e.target.value})
                        }}/><br></br>
                        <button type="submit" id="Error_Message1" className="btn btn-success">Submit</button><br></br>
                        <p style={{color:"red"}} id="Error_Message"></p>
                    </form>
                </div>
                :
                <div >
                    <h1>User's Enquiry Details</h1>
                    <table className="table-secondary" width="100%">
                    <tr>
                      
                            <th>NAME</th>
                            <th>EMAIL</th>
                            <th>PHONE NUMBER</th>
                            <th>QUERY</th>
                            <th>Whatsapp the user</th>
                       
                    </tr>
                    {this.state.users.map(user => {
                        return(
                            <tr>
                                <td>{user.name}</td>
                                <td>{user.email}</td>
                                <td><a href="tel:{user.phoneno}">{user.phoneno}</a></td>
                                <td>{user.query}</td>
                                <td><a href="https://wa.me/{user.phoneno}" rel="noreferrer" target="_blank">Whatsapp User</a></td>
                            
                            </tr>
                        )
                    })}
                    </table>
                    </div>
                    }
                 </div>
        );
    }
}