import React, { Component } from 'react';
import {BrowserRouter as Router,Route,Switch,Link} from 'react-router-dom';
import App1 from './comonennts/newApp';
import Admin from './comonennts/adminlogin';
import Spinner from './images/im1.png';

export default class App extends Component{
  constructor(){
    super();
    this.state = {
      islogged: false
    }
  }
  render(){
    return(
      <Router>
        {this.state.islogged === false ?
          <div className="row" style={{marginTop:"10%"}}>
            
            <div className="col-sm-6"><img src={Spinner} id="Mainimage" alt="Astrology" style={{width:"500px",minHeight:"100px",animation:"rotation 8s infinite linear"}}></img></div>

          <div className="col-sm-6" style={{marginTop:"5%"}}>
         
        <h1>Are you an User or an Admin</h1>
        <Link to='/UserLogin'><button id="btn" style={{width:"500px"}} className="btn btn-success" onClick = {()=>{
          this.setState({islogged:true})
        }}>User</button></Link><br></br><br></br>
        <Link to='/Adminlogin'><button style={{width:"500px"}} className="btn btn-warning" onClick = {()=>{
          this.setState({islogged:true})
        }}>Admin</button></Link>
      </div>  
          </div>
        
      :
      <div></div>
      }

      <Switch>
        <Route path="/UserLogin" component={App1} />
        <Route path="/Adminlogin" component={Admin} />
      </Switch>
      </Router>

    );
  }
}