import './App1.css';
import Header from './Header';
import Footer from './Footer';
import Enquiry from './Enquiry';
import {AiOutlineCopyrightCircle} from 'react-icons/ai'
import Officeaddress from './Officeaddress';
import About from './About';
import React,{ Component } from 'react';


export default class App1 extends Component {
  render(){
    return (
      <div className="App">     
        <div className="Header" style={{marginTop:"1%"}}>
          <Header />
        </div>
        <h3 style={{color:"red",fontStyle:"italic"}}>Our Services</h3>
  
        <div className="Services"  style={{marginTop:"1%"}}>
          <About />
        </div>
        
        <h3 style={{color:"red",fontStyle:"italic",marginTop:"1%"}}>Enquiry Form</h3>
        <div className="Body"  style={{marginTop:"1%"}}>
          <Enquiry />
        </div>
        <h3 style={{color:"red",fontStyle:"italic"}}>Office Details</h3>
        <div className="contact"  style={{marginTop:"1%"}}>
          <Officeaddress />
        </div>
        <h3 style={{color:"red",fontStyle:"italic"}}>Social Media</h3>
        <div className="Footer"  style={{marginTop:"1%"}}>
            <Footer />
        </div>
        <p><AiOutlineCopyrightCircle /> This website is built and owned by Siva Vignesh Siva Kumar</p>
        
      </div>
   
    );
  }
}

