import React, { Component } from 'react';
import {Navbar,NavLink,NavbarBrand ,Nav} from 'react-bootstrap'
import "./header.css";
import Spinner from '../images/im1.png';

export default class Header extends Component{
    render(){
        return(
                <div>
            <Navbar bg="dark" expand="lg" variant="dark" hegiht="150px">
            <NavbarBrand href="#home"><img src={Spinner} alt="Spinner" id="spinner" width="100px"></img></NavbarBrand>
            <NavbarBrand href="#home">Sakthi Jothida Nilayam</NavbarBrand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav" style={{height:"100px",fontSize:"20px"}} className= "sticky-top">
                <Nav className="mr-auto">
                <NavLink href="#aboutus">Our services</NavLink>
                <NavLink href="#body">Send an enquiry</NavLink>
                <NavLink href="#Contact">Contact</NavLink>
                </Nav>
            </Navbar.Collapse>
            </Navbar>
            </div>
 
        );
    }
}