import React, { Component } from 'react';
import './about.css';
import Mypic from '../images/Mypic.jpg';
export default class About extends Component {
    render() {
        return (
            <div className="row" id="aboutus" style={{marginTop:"3%"}}>
                <div id="image" className="col-lg-6">
                    <img alt="My Pic" src={Mypic} width="500px"></img>
                </div>
                <div className="col-lg-3" style={{marginTop:"3%",fontSize:"30px"}}>
                    <strong>
                    Astrology<br></br>
                    Numerology<br></br>
                    Pronology<br></br>
                    Vasthu Consultant<br></br>
                    Nameology
                    </strong>
                    </div>
                    <div className="col-lg-3" style={{marginTop:"3%",fontSize:"30px"}}>
                    <strong>
                    Mahabharatham<br></br>
                    Ramayanam<br></br>
                    Periyapuranam<br></br>
                    Spiritual Talks
                    </strong>
                    </div>
                </div>
                
        )
    }
}
